body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* SCROLL */
::-webkit-scrollbar { width: 5px; }
/* 스크롤바의 width */
::-webkit-scrollbar-track { background-color: #f0f0f0; }
/* 스크롤바의 전체 배경색 */
::-webkit-scrollbar-thumb { 
    background: linear-gradient(to bottom, #000, #e6e7e5); 
}
/* 스크롤바 색 */
::-webkit-scrollbar-button { display: none; }
/* 위 아래 버튼 (버튼 없애기를 함) */


/* 책 이름 */
.book-name{
    text-align: center;
    font-size: 40px;
    -webkit-animation-name:spin;
            animation-name:spin;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.book-nav {
    position: absolute;
    background-color: #dfdfdf;
    top: 0;
    left: 0;
    height: 100%;
    width: 300px;
    overflow-y: auto;
}

li,ol,ul{
    margin :0;
    display: block;
    padding-left: 15px;
    text-align :left;
    height:30px;
    line-height: 30px;
    -webkit-animation-name:slideUp;
            animation-name:slideUp;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.book-nav-chapter{
    border-radius: 0 40px 40px 0;
    -webkit-animation-name: fadeInUp;
            animation-name: fadeInUp;
    cursor : pointer;
    font-size: 20px;
    color: #a6aa9c;
    transition: all;
    transition-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.book-nav-chapter:hover {
    border-radius: 0 40px 40px 0;
    color: #000000;
    background-color: #f7d3ba;
    margin-right: 15px;
    border-left: 4px solid #cb3837;
    transition: all;
    transition-duration: 0.3s;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}


.book-nav-chapter-active {
    border-radius: 0 40px 40px 0;
    font-size: 20px;
    color: #000000;
    background-color: #f7d3ba;
    margin-right: 15px;
    border-left: 4px solid #cb3837;
    transition: all;
    transition-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

.book-nav-article{
    border-radius: 0 40px 40px 0;
    padding-left:20px;
    cursor : pointer;
    font-size: 13px;
    color: #a6aa9c;
    transition: all;
    transition-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

.book-nav-article:hover{
    border-radius: 0 40px 40px 0;
    padding-left:20px;
    color: #000000;
    background-color: #f7d3ba;
    margin-right: 15px;
    border-left: 4px solid #cb3837;
    transition: all;
    transition-duration: 0.3s;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

.book-nav-article-active{
    border-radius: 0 40px 40px 0;
    padding-left:20px;
    font-size: 13px;
    color: #000000;
    background-color: #f7d3ba;
    margin-right: 15px;
    border-left: 4px solid #cb3837;
    transition: all;
    transition-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

a{
    text-decoration: none;
}

.nav-line{
    height: 1px;
    margin: 0 30px;
    overflow: hidden;
    background: rgba(0,0,0,.07);
}


.cleaner {
    cursor: pointer;
}


.cleaner-left{
    background-color: rgba(0, 0, 0, 0);
    border: none; 
    position: fixed;
    cursor : pointer;
    top: 50%;
    left: 310px;
    opacity: 0.3;
}

.cleaner-left:hover{
    opacity:0.7;
}

.cleaner-left:focus{
    outline: none;
}

.cleaner-right{
    background-color: rgba(0, 0, 0, 0);
    border: none; 
    position: fixed;
    cursor : pointer;
    top:50%;
    right: 10px;
    opacity: 0.3;
}

.cleaner-right:hover{
    opacity:0.7;
}

.cleaner-right:focus{
    outline: none;
}
.content{
    padding-top: 50px;
    padding : 5%;
    width : 90%;
    overflow-x : hidden;
}

.mainframe{
    text-align: justify;
    word-break: keep-all;
    transition: all;
    transition-duration: 1s;
}

.mainhead{
    font-size: 40px;
}

.maintext{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    border-radius: 5px;
}
.maintext-up{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    -webkit-animation-name: slideUp;
            animation-name: slideUp;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.maintext-leftin{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    -webkit-animation-name:leftIn;
            animation-name:leftIn;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}

.maintext-leftout{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    -webkit-animation-name:leftOut;
            animation-name:leftOut;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}

.maintext-rightin{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    -webkit-animation-name:rightIn;
            animation-name:rightIn;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}

.maintext-rightout{
    padding:30px;
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    border-radius: 10px;
    background-color: #fff;
    -webkit-animation-name:rightOut;
            animation-name:rightOut;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}

.reply-up{
    -webkit-animation-name: slideUp;
            animation-name: slideUp;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}

.reply-form{
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    width: calc(100% - 14px);
    height: 70px;
    border : 1px #c9c9c9 solid;
    border-radius: 10px;
    transition: all;
    transition-duration: 1s;
}

.reply-form:focus{
    outline: none;
}

.reply-submit{
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    color : #000000;
    background-color: #f7d3ba;
    border: none; 
    border-radius: 10px;
    transition: all;
    transition-duration: 1s;
}

.reply-submit:hover{
    box-shadow: 3px 2px 3px 2px rgba(0,0,0,0.16), 3px 2px 5px 0 rgba(0,0,0,0.12);
    background-color: #fdb683;
    outline: none;
}

.reply-submit:focus{
    outline: none;
}
.book-wrapper{
    position: absolute;
    background-color: #f7f7f7;
    width: calc(100% - 300px);
    top: 0;
    height: 100%;
    left: 300px;
    overflow-y: auto;
}

.wrap-line{
    height: 1px;
    margin: 5px;
    overflow: hidden;
    background: rgba(0,0,0,.07);
  }
/* SCROLL */
::-webkit-scrollbar { width: 5px; }
/* 스크롤바의 width */
::-webkit-scrollbar-track { background-color :#f0f0f0; }
/* 스크롤바의 전체 배경색 */
::-webkit-scrollbar-thumb { 
    background: linear-gradient(to bottom, #a1a1a1, #a1a1a1); 
}
/* 스크롤바 색 */
::-webkit-scrollbar-button { display: none; }
/* 위 아래 버튼 (버튼 없애기를 함) */

@-webkit-keyframes slideUp{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes slideUp{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@-webkit-keyframes sizeUp{
  from{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
  to{
    -webkit-transform:scale(10);
            transform:scale(10);
  }
}

@keyframes sizeUp{
  from{
    -webkit-transform:scale(1);
            transform:scale(1);
  }
  to{
    -webkit-transform:scale(10);
            transform:scale(10);
  }
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
  50%{
    -webkit-transform: scale(1.5) rotate(0deg);
            transform: scale(1.5) rotate(0deg);
  }
  to {
    -webkit-transform: scale(1)rotate(0deg);
            transform: scale(1)rotate(0deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: scale(1) rotate(0deg);
            transform: scale(1) rotate(0deg);
  }
  50%{
    -webkit-transform: scale(1.5) rotate(0deg);
            transform: scale(1.5) rotate(0deg);
  }
  to {
    -webkit-transform: scale(1)rotate(0deg);
            transform: scale(1)rotate(0deg);
  }
}

@-webkit-keyframes leftIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes leftIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes leftOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes leftOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@-webkit-keyframes rightIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes rightIn {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes rightOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes rightOut {
  from {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

